<template>
  <div class="wrapper">
    <parallax class="section page-header header-filter" parallax-active="true" :style="headerStyle"></parallax>
    <div class="main main-raised">
      <div class="section profile-content">
        <div class="container">
          <div class="md-layout">
            <div class="md-layout-item md-size-50 md-small-size-100 mx-auto">
              <div class="profile">
                <div class="avatar">
                  <img :src="profile.dp" alt="Profile Image" class="img-raised rounded img-fluid">
                </div>
                <div class="name">
                  <h3 class="title">{{profile.name}}</h3>
                  <h6>{{profile.designation}}</h6>
                  <md-button
                    href="https://www.linkedin.com/in/anant-pawar"
                    class="md-just-icon md-simple md-linkedin"
                    target="_blank"
                  >
                    <i class="fab fa-linkedin"></i>
                  </md-button>
                  <md-button
                    href="https://github.com/anant-pawar"
                    class="md-just-icon md-simple md-github"
                    target="_blank"
                  >
                    <i class="fab fa-github"></i>
                  </md-button>
                  <md-button
                    href="Resume.pdf"
                    class="md-just-icon md-simple md-google"
                    target="_blank"
                  >
                    <i class="fas fa-file-pdf"></i>
                  </md-button>
                  <md-button
                    href="mailto:anant.c.pawar@gmail.com"
                    class="md-just-icon md-simple md-google"
                    target="_blank"
                  >
                    <i class="fas fa-envelope"></i>
                  </md-button>
                </div>
              </div>
            </div>
          </div>
          <div class="description text-center">
            <p>{{profile.about}}</p>
          </div>
          <div class="profile-tabs">
            <tabs
              :tab-active="1"
              :tab-name="[ 'Achivements', 'Experience', 'Skills']"
              :tab-icon="[ 'stars','work' , 'assessment']"
              plain
              nav-pills-icons
              color-button="success"
            >
              <template slot="tab-pane-1">
                <div class="md-layout">
                  <template v-for="certification in profile.certifications">
                    <div
                      class="md-layout-item md-size-40 md-small-size-100 mx-auto"
                      :key="certification"
                    >
                      <profile-card
                        card-plain
                        type="horizontal"
                        :shadow-normal="false"
                        :no-colored-shadow="false"
                        :card-image="certification.logo"
                      >
                        <template slot="cardContent">
                          <h4 class="card-title">{{certification.name}}</h4>
                          <h6 class="card-category text-muted">{{certification.validity}}</h6>
                          <h6 class="card-category text-muted">
                            <strong>Credential -</strong>
                            <a :href="certification.link" target="_blank">{{certification.id}}</a>
                          </h6>
                        </template>
                      </profile-card>
                    </div>
                  </template>
                </div>
              </template>

              <template slot="tab-pane-2">
                <div class="md-layout">
                  <template v-for="job in profile.jobs">
                    <div class="md-layout-item md-size-40 md-small-size-100 mx-auto" :key="job">
                      <profile-card
                        card-plain
                        type="horizontal"
                        :shadow-normal="false"
                        :no-colored-shadow="false"
                        :card-image="job.logo"
                      >
                        <template slot="cardContent">
                          <h4 class="card-title">{{job.designation}}</h4>
                          <h6 class="card-category text-muted">{{job.employer}}</h6>
                          <p class="card-description">{{job.duration}}</p>
                          <p class="card-description">{{job.location}}</p>
                        </template>
                      </profile-card>
                    </div>
                  </template>
                </div>
              </template>

              <template slot="tab-pane-3">
                <div class="md-layout">
                  <template v-for="skill in profile.skills">
                    <div
                      class="md-layout-item md-size-45 md-small-size-100 mx-auto skill"
                      :key="skill"
                    >
                      <profile-card
                        :shadow-normal="false"
                        :no-colored-shadow="true"
                        :card-image="skill.icon"
                      >
                        <template slot="cardContent">
                          <h4 class="card-title">{{skill.name}}</h4>
                          <md-table v-model="skill.technologies">
                            <md-table-row slot="md-table-row" slot-scope="{ item }">
                              <md-table-cell></md-table-cell>
                              <md-table-cell md-label="#">{{ item.id }}</md-table-cell>
                              <md-table-cell md-label="Name">{{ item.name }}</md-table-cell>
                              <md-table-cell md-label="Last Used">{{ item.lastUsed}}</md-table-cell>
                              <md-table-cell md-label="Self Eval">{{ item.eval}}</md-table-cell>
                              <md-table-cell></md-table-cell>
                            </md-table-row>
                          </md-table>
                        </template>
                      </profile-card>
                    </div>
                  </template>
                </div>
              </template>
            </tabs>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Tabs, ProfileCard } from "@/framework/components";
import Mixins from "@/framework/plugins/basicMixins";

export default {
  components: {
    Tabs,
    ProfileCard
  },
  mixins: [Mixins.HeaderImage],
  bodyClass: "profile-page",
  data() {
    return {
      profile: {
        name: "Anant Pawar",
        dp: require("@/app/assets/img/anant.jpg"),
        designation: "Specialist at Bosch Engineering and Business Solutions",
        about:
          "I intend to establish myself as a Software Professional in the information Technology field" +
          ", with an integrated business solution provider through a long time commitment" +
          ", I believe that my technical, functional and communication skills contributes to the projects designed " +
          "to enhance system functionality, company’s growth, end-user productivity and in turn ensuring personal growth within the organization.",
        jobs: [
          {
            designation: "Specialist",
            employer: "Bosch Engineering and Business Solutions",
            location: "Bangalore, India",
            duration: "Jan 2017 - Present",
            logo: require("@/app/assets/img/bosch.png")
          },
          {
            designation: "Software Product Owner",
            employer: "Bosch Connected Industry",
            location: "Stuttgart Area, Germany",
            duration: "Oct 2018 - Jan 2019",
            logo: require("@/app/assets/img/bosch.png")
          },
          {
            designation: "Senior Software Engineer",
            employer: "Tesco Bengaluru",
            location: "Bangalore, India",
            duration: "Jul 2014 - Jan 2017",
            logo: require("@/app/assets/img/tesco.png")
          },
          {
            designation: "Senior Software Engineer",
            employer: "Siemens Technology India",
            location: "Bangalore, India",
            duration: "Oct 2012 - Jan 2014",
            logo: require("@/app/assets/img/siemens.jpg")
          },
          {
            designation: "Software Engineer",
            employer: "Siemens Technology India",
            location: "Bangalore, India",
            duration: "Sep 2010 - Sep 2012",
            logo: require("@/app/assets/img/siemens.jpg")
          }
        ],
        certifications: [
          {
            name: "AWS Certified Developer Associate",
            organisation: "Amazon Web Service",
            validity: "Sep 2017 - Sep 2020",
            id: "15F62ZG122RQ1YGC",
            link:
              "https://www.certmetrics.com/amazon/public/badge.aspx?i=2&t=c&d=2017-09-10&ci=AWS00312443",
            logo: require("@/app/assets/img/aws.png")
          },
          {
            name: "AWS Certified Architect Associate",
            organisation: "Amazon Web Service",
            validity: "Oct 2017 - Oct 2020",
            id: "6YE8WFX2CJV4QGKH",
            link:
              "https://www.certmetrics.com/amazon/public/badge.aspx?i=2&t=c&d=2017-09-10&ci=AWS00312443",
            logo: require("@/app/assets/img/aws.png")
          }
        ],
        skills: [
          {
            name: "Databases",
            icon: require("@/app/assets/img/db.png"),
            technologies: [
              {
                id: 1,
                name: "Couchbase",
                lastUsed: "2018",
                eval: "2"
              },
              {
                id: 2,
                name: "Mongo DB",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 3,
                name: "MySQL",
                lastUsed: "2017",
                eval: "3"
              }
            ]
          },
          {
            name: "Cloud Services",
            icon: require("@/app/assets/img/cloud.png"),
            technologies: [
              {
                id: 1,
                name: "AWS EC2",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 2,
                name: "Daynamo DB",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 3,
                name: "RDS",
                lastUsed: "2017",
                eval: "3"
              }
            ]
          },
          {
            name: "Platform & Frameworks",
            icon: require("@/app/assets/img/gear.png"),
            technologies: [
              {
                id: 1,
                name: "Java",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 2,
                name: "Javascript",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 3,
                name: "Angular (6-7)",
                lastUsed: "2018",
                eval: "3"
              }
            ]
          },
          {
            name: "Operation Tools",
            icon: require("@/app/assets/img/deployment.png"),
            technologies: [
              {
                id: 1,
                name: "Docker",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 2,
                name: "Jenkins",
                lastUsed: "2018",
                eval: "3"
              },
              {
                id: 3,
                name: "Ansible",
                lastUsed: "2017",
                eval: "3"
              }
            ]
          }
        ]
      },
      image: require("@/app/assets/img/bg.jpeg")
    };
  }
};
</script>

<style lang="scss" scoped>
.section {
  padding: 0;
}

.profile-tabs /deep/ {
  .md-card-tabs .md-list {
    justify-content: center;
  }

  [class*="tab-pane-"] {
    margin-top: 3.213rem;
    padding-bottom: 50px;

    img:not(.img) {
      margin-bottom: 2.142rem;
    }
  }
}
</style>
